.paper-stack {
  background: #fff;
  box-shadow: 0 0 10px rgba(0,0,0,0.3);
  padding: 1.5em;
  position: relative;
}
.paper-stack:before, .paper-stack:after {
  content: "";
  height: 98%;
  position: absolute;
  width: 100%;
  z-index: -1;
}
.paper-stack:before {
  background: #fafafa;
  box-shadow: 0 0 8px rgba(0,0,0,0.2);
  left: -5px;
  top: 4px;
  transform: rotate(-2.5deg);
}
.paper-stack:after {
  background: #f6f6f6;
  box-shadow: 0 0 3px rgba(0,0,0,0.2);
  right: -3px;
  top: 1px;
  transform: rotate(1.4deg);
}


#about {
    position: relative;
    @media (min-width: 966px) {
        #portrait {
            img {
                display: none;
            }
            #portrait-img {
                background-image: url(/images/portrait-bg.jpg);
                background-size: 100%;
                background-position-y: 0;
                background-repeat: no-repeat;
                min-height: 50em;
            }
            .button.primary {
                display: none;
            }
        }
        #bio {
            position: absolute;
            top: 1em;
            left: 1em;
            width: 57%;
            background-color: rgba(255, 255, 255, 0.8);
            padding: 1.5em;
            border: 1px solid #ccc;
            // margin-top: 1em;
            // margin-left: 1em;
            text-align: justify;
            .button.primary {
                display: inline-block;
            }
        }
    }
    #portrait img {
        max-height: 200px;
    }
    @media (min-width: 720px) {
        #portrait img {
            max-height: 300px;
        }
    }
    @media (max-width: 965px) {
        #portrait {
            float: right;
            text-align: center;
            margin: 1rem 0 0 1em;

            img {
                display: inline;
                margin-bottom: .5em;
            }

            .button.primary {
                display: inline-block;
            }
        }
        #bio {
            position: static;
            width: 100%;
            background-color: rgba(255, 255, 255, 0.8);
            padding: 0em;
            border: none;
            .button.primary {
                display: none;
            }
        }
    }
}

