        .tag-list {
            display: none;
            // width: 120px;
            background-color: #eee;
            font-size: .8em;
            line-height: 1.5em;
            color: black;
            text-align: left;
            padding: 2px 1rem;
            border-radius: 3px;
            border-right: 3px solid #ddd;

            width: 20rem;
            left: 100%;
            top: 0;
            margin-left: 8px;

            /* Position the tooltip text - see below! */
            position: absolute;
            z-index: 1;
        }
        .tag-list::after {
            content: " ";
            position: absolute;
            right: 100%;
            top: 4px;
            margin-left: -5px;
            border-width: 5px;
            border-style: solid;
            border-color: transparent #eee transparent transparent;
        }
        .tag-list.downwards {
            display: none;
            // width: 120px;
            background-color: #eee;
            font-size: .8em;
            line-height: 1.5em;
            color: black;
            text-align: center;
            padding: 2px 0;
            border-radius: 3px;
            border-bottom: 3px solid #ddd;

            width: 120px;
            top: 100%;
            left: 50%;
            margin-top: .8em;
            margin-left: -60px;

            /* Position the tooltip text - see examples below! */
            position: absolute;
            z-index: 1;
        }
        .tag-list.downwards::after {
            content: " ";
            position: absolute;
            bottom: 100%;  /* At the top of the tooltip */
            left: 50%;
            margin-left: -5px;
            border-width: 5px;
            border-style: solid;
            border-color: transparent transparent #eee transparent;
        }
        .tags:hover .tag-list {
            display: block;
        }
        .tags:hover {
            color: black;
        }
